import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import supabase from "../config/supabaseCliente";

import estilo from "./styles/Menu.module.css";

function Menu() {
  var distritos = [
    "Lince",
    "SJM",
    "VMT",
    "Los Olivos",
    "Santa Anita",
    "SMP",
    "Surco",
    "Miraflores",
  ];

  const [activo, setActivo] = useState(false);
  const verActivo = () => setActivo(!activo);

  const [menu, setMenu] = useState(false);
  // const verMenu = () => setMenu(!menu);

  function verMenu() {
    setMenu(!menu);
  }

  const [input, setInput] = useState("");
  const [lista, setLista] = useState([]);

  var inputFinal;

  input === "" || input === " "
    ? (inputFinal = null)
    : (inputFinal = input
        .normalize("NFD")
        .replace(/[\u0300-\u0302\u0308\u030a\u0327]/g, "")
        .normalize("NFC"));

  useEffect(() => {
    const fetchLista = async () => {
      const { data, error } = await supabase
        .from("kines")
        .select()
        .or(
          `nombre.ilike.%${inputFinal}%,informacion.ilike.%${inputFinal}%,whatsapp.ilike.%${inputFinal}%,nacionalidad.ilike.%${inputFinal}%,descripcion.ilike.%${inputFinal}%`
        )
        .range(0, 12);

      if (error) {
        setLista([]);
      }
      if (data) {
        setLista(data);
      }
    };

    fetchLista();
  }, [inputFinal]);

  return (
    <div className="menu_principal_contenedor">
      <div className={estilo.menu}>
        <div className={estilo.menu_contenedor}>
          <div className={estilo.menu_titulo}>
            <div
              className={
                activo ? `${estilo.barras} ${estilo.cambiar}` : estilo.barras
              }
              onClick={verActivo}
            >
              <div className={estilo.barra_a}></div>
              <div className={estilo.barra_b}></div>
              <div className={estilo.barra_c}></div>
            </div>

            <Link to={"/"}>
              <h1>Kinesiologas</h1>
            </Link>
          </div>

          <div className={estilo.contenedor_derecho}>
            <div className={estilo.buscador}>
              <input placeholder="Buscar kinesiologa..." />
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="20"
                viewBox="0 0 16 16"
              >
                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
              </svg>
            </div>

            <Link to={"/anuncia"}>
              <div className={estilo.anuncia}>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                >
                  <path d="M6 8a.5.5 0 0 0 .5.5h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 0 0-.708.708L12.293 7.5H6.5A.5.5 0 0 0 6 8Zm-2.5 7a.5.5 0 0 1-.5-.5v-13a.5.5 0 0 1 1 0v13a.5.5 0 0 1-.5.5Z" />
                </svg>
                <p>Anuncia</p>
              </div>
            </Link>
          </div>

          <div className={estilo.botones}>
            <svg
              className={menu ? estilo.boton_close : estilo.boton_ver}
              onClick={verMenu}
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              viewBox="0 0 16 16"
            >
              <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
            </svg>
            <svg
              className={menu ? estilo.boton_ver : estilo.boton_close}
              onClick={verMenu}
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              viewBox="0 0 16 16"
            >
              <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm3.354 4.646L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 1 1 .708-.708z" />
            </svg>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              viewBox="0 0 16 16"
            >
              <path d="M12 0H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2zM7.5 6.707 6.354 7.854a.5.5 0 1 1-.708-.708l2-2a.5.5 0 0 1 .708 0l2 2a.5.5 0 0 1-.708.708L8.5 6.707V10.5a.5.5 0 0 1-1 0V6.707z" />
            </svg>
          </div>
        </div>

        <div className={menu ? estilo.buscador_barra : estilo.no_buscador}>
          <div className={estilo.buscar_contenido}>
            <input
              onChange={(e) => setInput(e.target.value)}
              type="text"
              value={input}
              placeholder="Buscar kinesiologa..."
            />
          </div>

          {/* buscador automatico */}
          {lista.length === 0 ? null : (
            <div className={estilo.resultado_busqueda}>
              {lista.map((e) => (
                <div key={e.id} className={estilo.resultado_contenido}>
                  <div className={estilo.resultado_portada}>
                    <img src={e.portada} alt={e.nombre + "-" + e.telefono} />
                  </div>

                  <div className={estilo.resultado_informacion}>
                    <h3>
                      {e.nombre} - {e.telefono}
                    </h3>

                    <div className={estilo.resultado_informacion_datos}>
                      {e.nacionalidad === null ? null : (
                        <span>{e.nacionalidad}</span>
                      )}
                      {e.edad === null ? null : <span>{e.edad}</span>}
                      {e.distritos.map((e) => (
                        <span key={e.distrito}>{e.distrito}</span>
                      ))}
                    </div>
                    <p>{e.informacion}</p>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        <div className={activo ? estilo.lista : estilo.no_lista}>
          <Link to={"/"}>
            <div className={estilo.inicio} onClick={verActivo}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                viewBox="0 0 16 16"
              >
                <path d="M8.707 1.5a1 1 0 0 0-1.414 0L.646 8.146a.5.5 0 0 0 .708.708L2 8.207V13.5A1.5 1.5 0 0 0 3.5 15h9a1.5 1.5 0 0 0 1.5-1.5V8.207l.646.647a.5.5 0 0 0 .708-.708L13 5.793V2.5a.5.5 0 0 0-.5-.5h-1a.5.5 0 0 0-.5.5v1.293L8.707 1.5ZM13 7.207V13.5a.5.5 0 0 1-.5.5h-9a.5.5 0 0 1-.5-.5V7.207l5-5 5 5Z" />
              </svg>
              <p>Inicio</p>
            </div>
          </Link>

          <div>
            <div className={estilo.distritos}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 16 16"
              >
                <path d="M15.817.113A.5.5 0 0 1 16 .5v14a.5.5 0 0 1-.402.49l-5 1a.502.502 0 0 1-.196 0L5.5 15.01l-4.902.98A.5.5 0 0 1 0 15.5v-14a.5.5 0 0 1 .402-.49l5-1a.5.5 0 0 1 .196 0L10.5.99l4.902-.98a.5.5 0 0 1 .415.103zM10 1.91l-4-.8v12.98l4 .8V1.91zm1 12.98 4-.8V1.11l-4 .8v12.98zm-6-.8V1.11l-4 .8v12.98l4-.8z" />
              </svg>
              <p>Distritos</p>
            </div>

            <div className={estilo.nombres_distritos}>
              {distritos.sort().map((e) => (
                <h4 key={e}>{e}</h4>
              ))}
            </div>
          </div>

          <div className={estilo.catalogo}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 16 16"
            >
              <path d="M1 2.828c.885-.37 2.154-.769 3.388-.893 1.33-.134 2.458.063 3.112.752v9.746c-.935-.53-2.12-.603-3.213-.493-1.18.12-2.37.461-3.287.811V2.828zm7.5-.141c.654-.689 1.782-.886 3.112-.752 1.234.124 2.503.523 3.388.893v9.923c-.918-.35-2.107-.692-3.287-.81-1.094-.111-2.278-.039-3.213.492V2.687zM8 1.783C7.015.936 5.587.81 4.287.94c-1.514.153-3.042.672-3.994 1.105A.5.5 0 0 0 0 2.5v11a.5.5 0 0 0 .707.455c.882-.4 2.303-.881 3.68-1.02 1.409-.142 2.59.087 3.223.877a.5.5 0 0 0 .78 0c.633-.79 1.814-1.019 3.222-.877 1.378.139 2.8.62 3.681 1.02A.5.5 0 0 0 16 13.5v-11a.5.5 0 0 0-.293-.455c-.952-.433-2.48-.952-3.994-1.105C10.413.809 8.985.936 8 1.783z" />
            </svg>
            <p>Catalogo</p>
          </div>
        </div>
      </div>
      <div className={estilo.espacio}></div>
    </div>
  );
}

export default Menu;
