import React from "react";

import estilo from "./styles/Tarjetas.module.css";

function Tarjetas({ lista }) {
  function handleClickDerecho(e) {
    e.preventDefault();
  }

  return (
    <div className={estilo.tarjeta}>
      <figure>
        <img
          onContextMenu={handleClickDerecho}
          src={lista.portada}
          alt={
            lista.nombre +
            "-" +
            lista.nacionalidad +
            "-" +
            lista.telefono +
            "-kinesiologa"
          }
        />
        {lista.verificacion ? (
          <span className={estilo.verificada}>Verificada</span>
        ) : (
          <span className={estilo.no_verificada}>No Verificada</span>
        )}

        {lista.nacionalidad == null ? null : (
          <span className={estilo.nacionalidad}>{lista.nacionalidad}</span>
        )}
        <p className={estilo.etiqueta}>Kinesiologas</p>
      </figure>

      <div className={estilo.datos}>
        <h2>{lista.nombre}</h2>

        <div className={estilo.distritos}>
          {lista.distritos.map((e) => (
            <p key={e.distrito}>{e.distrito}</p>
          ))}
        </div>

        <div className={estilo.informacion}>
          <p>{lista.informacion}</p>
        </div>
      </div>
    </div>
  );
}

export default Tarjetas;
