import React, { useEffect, useState } from "react";
import Tarjetas from "../components/Tarjetas";

import supabase from "../config/supabaseCliente";
import estilo from "./styles/Inicio.module.css";

function Inicio() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const [lista, setLista] = useState([]);
  const [pagina, setPagina] = useState(5);

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop =
        (document.documentElement && document.documentElement.scrollTop) ||
        document.body.scrollTop;

      const scrollHeight =
        (document.documentElement && document.documentElement.scrollHeight) ||
        document.body.scrollHeight;

      const clientHeight =
        document.documentElement.clientHeight || window.innerHeight;

      if (scrollTop + clientHeight >= scrollHeight) {
        setPagina(pagina + 2);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [pagina]);

  // useEffect(() => {
  //   const handleScroll = () => {
  //     if (
  //       window.innerHeight + document.documentElement.scrollTop ===
  //       document.documentElement.offsetHeight
  //     ) {
  //       setPagina(pagina + 2);
  //     }
  //   };
  //   window.addEventListener("scroll", handleScroll);

  //   return () => {
  //     window.removeEventListener("scroll", handleScroll);
  //   };
  // }, [pagina]);

  useEffect(() => {
    const fetchLista = async () => {
      const { data, error } = await supabase
        .from("kines")
        .select()
        .order("id", { ascending: false })
        .range(0, pagina);

      if (error) {
        console.log(error);
      }
      if (data) {
        setLista(data);
      }
    };

    fetchLista();
  }, [pagina]);

  return (
    <div>
      {lista && (
        <div className={estilo.tarjetas}>
          {lista.map((e) => (
            <Tarjetas key={e.id} lista={e}></Tarjetas>
          ))}
        </div>
      )}

      <h1>el mundo esta loco</h1>
      <ul>
        <li>abeja</li>
        <li>perro</li>
      </ul>
    </div>
  );
}

export default Inicio;
